.userMain {
    font-size: 14px;
  }
  
  .userMain__menu {
    height: 46px;
    display: flex;
    background-color: #fff;
    align-items: center;
    color: #111;
    font-size: 14px;
    position: relative;
  }
  
  .userMain__tab {
    flex: 1;
    height: 100%;
    line-height: 46px;
  }
  
  .userMain__title {
    position: absolute;
    top: 0;
    width: 25%;
    text-align: center;
  }
  
  .userMain__title--active {
    border-bottom: 2px solid #f63;
    color: #f63;
  }
  
  .userMain__content {
    list-style: none;
  }
  
  .userMain__emptyIcon {
    margin: 88px auto 0;
    height: 100px;
    width: 100px;
    background-position: center;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    background-image: url("data:image/png;base64,");
  }
  
  .userMain__emptyText1 {
    font-size: 16px;
    color: #777;
    line-height: 16px;
    text-align: center;
    margin: 16px 0 12px;
  }
  
  .userMain__emptyText2 {
    font-size: 15px;
    color: #999;
    line-height: 15px;
    text-align: center;
  }